<template>
  <div :style="{background: 'url(' + background + '),center 0' }" style="height: auto">
    <el-button type="text" style="margin: 10px 20px; color: white" @click="goBack()"><i class="el-icon-arrow-left"></i>返回首页
    </el-button>
    <div class="header">
      <div class="item" style="font-weight: bold;">
        <span style="color: #67C23A;font-size: 30px;">{{name}}</span>
        <span style="color: white">{{tableData.aqitype}}</span>
      </div>
      <div class="item" style="color: #C0C4CC;">
        <span>数据更新时间：{{tableData.time}}</span>
      </div>
      <div class="item detail">
        <div class="item">
          <div class="value">{{tableData.aqi}}</div>
          <diV class="caption">AQI</diV>
        </div>
        <div class="item">
          <div class="value">{{tableData.pm25per1h}}</div>
          <diV class="caption">PM2.5/1h</diV>
        </div>
        <div class="item">
          <div class="value">{{tableData.pm10per1h}}</div>
          <diV class="caption">PM10/1h</diV>
        </div>
        <div class="item">
          <div class="value">{{tableData.coper1h}}</div>
          <diV class="caption">CO/1h</diV>
        </div>
        <div class="item">
          <div class="value">{{tableData.no2per1h}}</div>
          <diV class="caption">NO2/1h</diV>
        </div>
        <div class="item">
          <div class="value">{{tableData.o3per1h}}</div>
          <diV class="caption">O3/1h</diV>
        </div>
        <div class="item">
          <div class="value">{{tableData.o3per8h}}</div>
          <diV class="caption">O3/8h</diV>
        </div>
        <div class="item">
          <div class="value">{{tableData.so2per1h}}</div>
          <diV class="caption">SO2/1h</diV>
        </div>
        <div class="clear"></div>
        <div style="color: #DCDFE6;margin-left: 5px;margin-bottom: 10px">数值单位：μg/m3(CO为mg/m3)</div>
        <div style="color: #C0C4CC;margin-left: 5px">
          首要污染物: {{tableData.majorPollutant}}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import background from '../assets/background.jpg'
  import Pubsub from 'pubsub-js'

  export default {
    name: "Header",
    props: ["name"],
    data() {
      return {
        tableData: {
          place: '科干所',
          aqi: 79,
          aqitype: '良',
          majorPollutant: '臭氧1小时',
          pm25per1h: 10,
          pm10per1h: 29,
          coper1h: 0.5,
          no2per1h: 18,
          o3per1h: 183,
          no2per1h: 132,
          so2per1h: 8,
          o3per8h: '',
          time: ''
        },
        background: background,
      }
    }, computed: {
      compare() {
        /*
         pm25per1h: 10,
                pm10per1h: 29,
                coper1h: 0.5,
                no2per1h: 18,
                o3per1h: 183,
                no2per1h: 132,
                so2per1h: 8,
         */
        this.compareAB(this.tableData.pm10per1h, this.tableData.pm25per1h)
      }

    },
    methods: {
      goBack() {
        this.$router.push({
          name: `Index`
        })
      }
    }
    ,
    compareAB(a, b) {
      if (parseInt(a) > parseInt(b)) {
        return a
      } else {
        return b
      }

    }
    ,
    mounted() {
      Pubsub.subscribe("place", (event, data) => {
        this.tableData = data
      })
    }

  }
</script>

<style scoped>
  .header {
    padding: 40px 100px;
  }

  .header .item {
    margin: 10px 50px
  }

  .header .detail .item {
    margin: 10px 40px 10px 0;
    border-radius: 50% 50% 50% 50%;
    background-color: rgba(255, 255, 255, 0.25);
    width: 100px;
    height: 100px;
    float: left;
  }

  .header .detail .item .value {
    color: white;
    text-align: center;
    font-size: 30px;
    margin-top: 20px;
  }

  .header .detail .item .caption {
    text-align: center;
    font-size: 14px;
    margin-top: 10px;
  }

  .clear {
    clear: both
  }
</style>



