<template>
  <div>
    <Header :name="name"></Header>
    <Main :name="name"></Main>

  </div>
</template>

<script>
  import Header from './Header'
  import Main from "./Main"

  export default {
    name: "Details",
    components: {Main, Header},
    data() {
      return {
        name: ""
      }
    },
    mounted() {
      this.name = this.$route.query.name
    }
  }
</script>

<style scoped>

</style>



