<template>
  <div class="content">
    <div class="date block">
      <span class="demonstration">日期时间：</span>
      <el-date-picker
        v-model="date"
        type="date"
        placeholder="选择日期">
      </el-date-picker>
      <el-time-select
        v-model="time"
        :picker-options="{
          start: '00:00',
          step: '01:00',
          end: '23:00'
         }"
        placeholder="选择时间">
      </el-time-select>
    </div>
    <div class="title">{{tableData.length}}个监测点</div>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column
        prop="place"
        label="监测点"
        width="200">
      </el-table-column>
      <el-table-column
        prop="aqi"
        label="AQI"
        width="100">
      </el-table-column>
      <el-table-column
        prop="aqitype"
        label="空气质量指数类别">
      </el-table-column>
      <el-table-column
        prop="majorPollutant"
        label="首要污染物">
      </el-table-column>
      <el-table-column
        prop="pm25per1h"
        label="PM2.5细颗粒物">
      </el-table-column>
      <el-table-column
        prop="pm10per1h"
        label="PM10可吸入颗粒物">
      </el-table-column>
      <el-table-column
        prop="coper1h"
        label="CO一氧化碳">
      </el-table-column>
      <el-table-column
        prop="no2per1h"
        label="NO2二氧化氮">
      </el-table-column>
      <el-table-column
        prop="o3per1h"
        label="O3臭氧1小时平均">
      </el-table-column>
      <el-table-column
        prop="no2per1h"
        label="O3臭氧8小时平均">
      </el-table-column>
      <el-table-column
        prop="so2per1h"
        label="SO2二氧化硫">
      </el-table-column>
    </el-table>
    <div class="data">
      <div style="font-size: 18px;color: #32CD74">近24小时AQI指数变化</div>
      <ECharts ref="chart1" :options="option" :auto-resize="true" style="width: 100%;height: 480px"></ECharts>
    </div>
  </div>
</template>

<script>
  import ECharts from 'vue-echarts/components/ECharts'
  import axios from 'axios'
  import moment from "moment"
  import Pubsub from 'pubsub-js'

  export default {
    name: "Main",
    props: ["name"],
    components: {
      ECharts
    },
    data() {
      return {
        date: '2014-08-29',
        time: '',
        option: {
          tooltip: {
            trigger: 'item'
          },
          xAxis: {
            name: '时间',
            data: [],
            type: "category",
          },
          yAxis: {
            name: 'AQI（μg/m3）',
            data: [0, 50, 100, 150, 200, 250, 300, 350, 400],
            type: 'value'
          },
          series: [{
            data: [],
            type: 'line',
            smooth: true
          }]
        },

        tableData: [{
          place: '科干所',
          aqi: 79,
          aqitype: '良',
          majorPollutant: '臭氧1小时',
          pm25per1h: 10,
          pm10per1h: 29,
          coper1h: 0.5,
          no2per1h: 18,
          o3per1h: 183,
          no2per1h: 132,
          so2per1h: 8,
        }],
      }
    },

    watch: {
      name(val) {
        if (val) {
          let station = val + "市"
          this.listPlaceByTimeAndArea(station, null)
          //获取折线图的数据
          station = station + "（总）"
          this.listAreaAQIByTime(station, null)
        }
      },
      time(val) {
        if (val) {
          let date = moment(this.date).format("YYYY-MM-DD") + " " + val
          let station = this.name + "市"
          this.listPlaceByTimeAndArea(station, date)
          station = station + "（总）"
          this.listAreaAQIByTime(station, date)
        }
      },
      date(val) {
        if (val) {
          let date = moment(val).format("YYYY-MM-DD") + " " + this.time
          let station = this.name + "市"
          this.listPlaceByTimeAndArea(station, date)
          station += "（总）"
          this.listAreaAQIByTime(station, date)
        }
      }
    },
    methods: {
      listPlaceByTimeAndArea(station, date) {
        axios.post("/PM25_spring/listPlaceByTimeAndArea", {station, date}).then((res) => {
          Pubsub.publish("place", res.data[0])
          res.data.splice(0, 1)
          this.tableData = res.data
        }).catch(e => {

        })
      },
      listAreaAQIByTime(station, date) {
        let aqiList = []
        let timeList = []
        axios.post("/PM25_spring/listAreaAQIByTime", {station, date}).then((res) => {
          let len = res.data.length
          res.data.forEach((item, index) => {
            let dateTime = item.time.split(" ")
            aqiList.push(item.aqi)
            timeList.push(dateTime[1])
            if (index == len - 1) {
              this.date = dateTime[0]
              let hour = dateTime[1].slice(0, dateTime[1].length - 1)[0]
              this.time = ((hour.length == 1) ? "0" + hour : hour) + ":00"
            }
          })
          this.option.xAxis.data = timeList
          this.option.series[0].data = aqiList
        }).catch(e => {

        })
      }

    }

  }
</script>

<style scoped>
  .content {
    margin: 10px 100px;
  }

  .content .title {
    margin-left: 20px;
    color: #67C23A;
    font-size: 24px;
  }

  .content .date {
    float: right;
    color: #67C23A;
    font-size: 14px;
  }
  .data{
    margin-top: 30px;
    padding-top: 10px;
    text-align: center;
    width: 100%;
  }
</style>



