import Vue from 'vue'
import Router from 'vue-router'
// import Index3D from "../components/Index3D"
import Index from "../components/Index"
import Details from '../components/Details'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/index'
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/details',
      name: "Details",
      component: Details
    },
  ]
})



